import React from 'react'
import "./Categories.scss"
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import Dashhead from '../Dashhead';
import axios from 'axios'
import {connect} from 'react-redux'
import DeleteOutlinedIcon from '@mui/icons-material/DeleteOutlined';
import {TextField,Button,Alert} from '@mui/material'
import AddRoundedIcon from '@mui/icons-material/AddRounded';
import moment from 'moment'
import {useForm} from 'react-hook-form'
function Categories(props) {
    const [display,setDisplay]=React.useState(false)
    const {handleSubmit,register,formState:{errors}}=useForm()
    const [data,setData]=React.useState([])
    const [error,setError]=React.useState("")

    const getCategories = ()=>{
        axios.get(`${process.env.REACT_APP_DEVELOPMENT}/api/category/all`,{headers:{token:props.user.user}})
        .then(res=>{
            console.log(res);
            setData(res.data.result)
        })
        .catch(err=>{
            console.log(err);
            setError("Something went wrong")
        })
    }

    React.useEffect(()=>{
       getCategories()
    },[])
    const onSubmit = ({name})=>{
        console.log(name);
        axios.post(`${process.env.REACT_APP_DEVELOPMENT}/api/category/create`,{name},{headers:{token:props.user.user}})
        .then(res=>{
            console.log(res)
           getCategories()
        })
        .catch(err=>{
            console.log(err)
            setError("Something went wrong")
        })
    }
    const deleteCategory = (categoryId)=>{
        axios.post(`${process.env.REACT_APP_DEVELOPMENT}/api/category/delete`,{categoryId},{headers:{token:props.user.user}})
        .then(res=>{
            console.log(res)
            getCategories()
        })
        .catch(err=>{
            console.log(err)
        })
    }
    console.log(data)
    return (
        <div className="row">
            <div className="col-xs-12 col-sm-12 col-md-2 col-lg-2 col-xl-2">
            <Dashhead id={3} display={display} />
            </div>

            <div className="col-xs-12 col-sm-12 col-md-10 col-lg-10 col-xl-10 dashboard-container" onClick={()=>display&&setDisplay(false)}>
            <span className="iconbutton display-mobile">
            <IconButton  size="large" aria-label="Menu" onClick={()=>setDisplay(true)}>
            <MenuIcon fontSize="inherit" />
             </IconButton>
             </span>

            <section className="category-section">
                <h1 className="heading">Categories</h1>
                <form onSubmit = {handleSubmit(onSubmit)}>
                <TextField className='textfield' error={errors.name&&true} {...register("name",{required:true})} variant="filled" id='filled-basic' label="Category Name" fullWidth />
                {error.length>0&&<Alert className="alert" severity="error">{error}</Alert>}
                <Button className="btn" startIcon={<AddRoundedIcon />} type="submit" variant="contained">Add Category</Button>
                </form>

                <section className='category-row'>
                {
                    data.length>0?(
                        data.map((item,index)=>(
                            <div>
                            <div className="row m-auto align-items-center category-item">
                                <div className="col-1">
                                    <p className="index">{index+1}</p>
                                </div>
                                <div className="col-2">
                                    <p className="name">{item.name}</p>
                                </div>
                                <div className="col-2">
                                    <p className="id">{item._id}</p>
                                </div>
                                <div className="col-6">
                                    <p className="time">{moment.parseZone(item.createdAt).local().format("dddd, MMMM Do YYYY, h:mm:ss a")}</p>
                                </div>
                                <div className="col-1">
                                    <IconButton onClick={()=>deleteCategory(item._id)}>
                                    <DeleteOutlinedIcon />
                                    </IconButton>
                                </div>
                            </div>
                            {index!==data.length-1&&<hr />}
                            </div>
                        ))
                    ):<div>No Categories</div>
                }
                </section>


            </section>

            



             </div>
    </div>
    )
}

const mapStateToProps = ({EventUser})=>{
    return {
        user:EventUser
    }
}

export default connect(mapStateToProps)(Categories)
