import React from 'react'
import "./Products.scss"
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import Dashhead from '../Dashhead';
import axios from 'axios'
import {connect} from 'react-redux'
import {Fab,Tooltip} from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import { DataGrid } from '@mui/x-data-grid';
import moment from 'moment';
function Products(props) {
    const [display,setDisplay]=React.useState(false)
    const [data,setData]=React.useState([])

    React.useEffect(() =>{
        axios.get(`${process.env.REACT_APP_DEVELOPMENT}/api/product/all`,{headers:{token:props.user.user}})
        .then(res=>{
            console.log(res)
            let arr = res.data.result.map((item,index)=>({...item,id:index+1}))
            setData(arr)
    
        })
        .catch(err=>{
            console.log(err);
        })
    },[])

    
    console.log(data)
    return (
        <div className="row">
            <div className="col-xs-12 col-sm-12 col-md-2 col-lg-2 col-xl-2">
            <Dashhead id={2} display={display} />
            </div>

            <div className="col-xs-12 col-sm-12 col-md-10 col-lg-10 col-xl-10 dashboard-container" onClick={()=>display&&setDisplay(false)}>
            <span className="iconbutton display-mobile">
            <IconButton  size="large" aria-label="Menu" onClick={()=>setDisplay(true)}>
            <MenuIcon fontSize="inherit" />
             </IconButton>
             </span>


            <section>
                <h1 className="heading">All Products</h1>

                <div style={{ height: '80vh', width: '100%' }}>
                <DataGrid
                    rows={data}
                    columns={columns}
                    autoPageSize
                    onRowClick={(item,ev)=>props.history.push("/productdetail",item.row)}
                />
                </div>




            <div style={{position:"fixed",bottom:"5%",right:"5%"}}>
              <Tooltip title="Create Product">
              <Fab onClick={()=>props.history.push("/createproduct")} color="primary" aria-label="add">
                <AddIcon />
              </Fab>
              </Tooltip>
              </div>
              </section>            



    </div>
    </div>
    )
}

const mapStateToProps =({EventUser})=>{
    return {
        user:EventUser
    }
}

export default connect(mapStateToProps)(Products)
const columns = [
    { field: 'id', headerName: 'ID',width:20},
    { field: 'name', headerName: 'Product Name',width:300},
    { field: 'brand', headerName: 'Brand Name',valueGetter:(param)=>param.value.name,width:150},
    {field:"attributes",headerName:"Product Attributes",valueGetter:(param)=>Object.keys(param.value[0]),width:150},
    {field:"reviewsCount",headerName:"Reviews",width:80},
    {field:"rating",headerName:"Ratings",valueGetter:(param)=>param.value.total,width:70},
    {field:"variations",headerName:"Variations",valueGetter:(param)=>param.value.length,width:90},
    {field:"available",headerName:"Available",valueGetter:(param)=>param.value?param.value.length:0,width:90},
    {field:"imgs",headerName:"Imgs",valueGetter:(param)=>param.value.length,width:90},
    {field:"updatedAt",headerName:"Last Updated",valueGetter:(param)=>moment.parseZone(param.value).local().format("DD/MM/YY"),width:120}


  ];
  
