import React from 'react'
import "./ProductDetail.scss"
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import Dashhead from '../Dashhead';
import {Chip,Button} from '@mui/material'
import _ from 'lodash'
import Rating from '@mui/material/Rating';
import { styled } from '@mui/material/styles';
import DeleteIcon from '@mui/icons-material/Delete';
import CameraAltIcon from '@mui/icons-material/CameraAlt';
import CheckIcon from '@mui/icons-material/Check';
import {Fab,Tooltip} from '@mui/material';
import { useForm } from 'react-hook-form';
import TwoBDialog from '../utils/TwoBDialog'
import {connect} from 'react-redux'
import axios from 'axios'
import moment from 'moment';
const Input = styled('input')({
    display: 'none',
  });
function ProductDetail(props) {
    const [display,setDisplay]=React.useState(false)
    const [variationId,setVariationId]=React.useState("")
    const [selectedImage,setSelectedImage]=React.useState([])
    const {handleSubmit,register,formState:{errors}}=useForm()
    const [deleteImage,setDeleteImage]=React.useState("")
    const [details,setDetails]=React.useState({})
    const [open,setOpen]=React.useState(false)
    console.log("detail",details,variationId,props)

    const getSingleProd = ()=>{
        axios.post(`${process.env.REACT_APP_DEVELOPMENT}/api/product/single`,{productId:props.location.state._id},{headers:{token:props.user.user}})
        .then(res=>{
            console.log(res)
            if(res.data.msg!=="Error"){
                if(res.data.result.length>0){
                    let arr = res.data.result[0].variations.map(item=>({...item,options:{...item.options,images:""}}))
                    res.data.result[0].variations=arr;
                    //console.log(res.data)
                    setDetails(res.data.result[0])
                }
                

            }
        })
        .catch(err=>{
            console.log(err);
        })
    }

    React.useEffect(() =>{
       getSingleProd()
    },[])

    const onSubmit = ()=>{
        const formdata = new FormData();
        if(!Array.isArray(selectedImage)){
            formdata.append('image',selectedImage)
            formdata.append('variationId',variationId)
          console.log(formdata)
          axios.post(`${process.env.REACT_APP_DEVELOPMENT}/api/variation/uploadImg`,formdata,{headers:{Accept:'application/json','Content-Type':"multipart/form-data",token:props.user.user}})
          .then(res=>{
              console.log(res);
              getSingleProd()
            setSelectedImage([])
            setVariationId("")
          })
          .catch(err=>{
              console.log(err);
          })
        }
    }

    const handleImageDelete = ()=>{
        console.log(variationId,deleteImage);
        axios.post(`${process.env.REACT_APP_DEVELOPMENT}/api/variation/deleteImg`,{variationId,img:deleteImage})
        .then(res=>{
            console.log(res)
            getSingleProd()
            setOpen(false)
        })
        .catch(err=>{
            console.log(err)
        })
    }

    return (
        <div className="row">
            <TwoBDialog open={open} setOpen={setOpen} 
            handleSubmit={handleImageDelete}
            title="Remove Product Image" 
            description="Are you sure you want to remove this image" 
            rightButton="Remove"
            leftButton="Cancel"
            />
            <div className="col-xs-12 col-sm-12 col-md-2 col-lg-2 col-xl-2">
            <Dashhead id={2} display={display} />
            </div>

            <div className="col-xs-12 col-sm-12 col-md-10 col-lg-10 col-xl-10 dashboard-container" onClick={()=>display&&setDisplay(false)}>
            <span className="iconbutton display-mobile">
            <IconButton  size="large" aria-label="Menu" onClick={()=>setDisplay(true)}>
            <MenuIcon fontSize="inherit" />
             </IconButton>
             </span>


            {!_.isEmpty(details)&&<section className="product-detail">
                 <h2>{details.name}</h2>
                 <Button onClick={()=>props.history.push("/editproduct",details)}>Edit Product</Button>
                 <Button variant="outlined" onClick={()=>{
                     axios.post(`${process.env.REACT_APP_DEVELOPMENT}/api/product/deleteProductData`,{productId:details._id},{headers:{token:props.user.user}})
                     .then(res=>{
                         console.log(res)
                         props.history.push("/products")
                     })
                     .catch(err=>{
                         console.log(err)
                     })
                 }}>Delete Product</Button>
                 <p className="createdat">
                     <span className="greyspan">Created on: </span> {moment.parseZone(details.createdAt).local().format("dddd, MMMM Do YYYY, h:mm:ss a")} <span className="ml-2 greyspan">Last Updated:</span> {moment.parseZone(details.updatedAt).local().format("dddd, MMMM Do YYYY, h:mm:ss a")}
                 </p>
                 <Rating name="read-only" value={details.rating.total} readOnly />
                 <p><span className="greyspan">Avg:</span> {details.rating.avg} <span className="ml-2 greyspan">Count:</span> {details.rating.count}</p>
                 <p className="greyspan category">{details.category.name}</p>
                 <Chip label={details.brand.name}/>

                <h4>Offers</h4>
                {
                    details.offers.map((item,index)=><p key={index} className='offers'>{index+1}: {item}</p>)
                }

                <h4>EMI Options</h4>
                <section className="row m-auto">
                {
                    !_.isEmpty(details.EMI_Options)&&details.EMI_Options.map((item,index)=>(
                        <div className="emi-option">
                            <p><span className="greyspan">Monthly Tenure: </span>{item.monthCount}</p>
                            <p><span className="greyspan">Downpayment: </span>{item.downPayment}</p>
                        </div>
                    ))
                }
                </section>

                <h4>Attributes</h4>
                {
                    !_.isEmpty(details.attributes)&&_.map(details.attributes[0],(value,key)=>(
                        <div className="row m-auto attr-cont">
                            <div className="col-3 attr-key">
                                <p>{key}</p>
                            </div>
                            <div className="col-9 row m-auto attr-value">
                                {
                                    value.map((item,ind)=><div><p className="ml-2" key={ind}>{item}</p></div>)
                                }
                            </div>
                        </div>
                    ))
                }

            <section className="varation-section">
                <h4>Product Varations</h4>
                {
                    !_.isEmpty(details.variations)&&details.variations.map((item,index)=>(
                        <div className="row m-auto singlevar" key={index}>
                        {_.map(item.options,(value,key)=>(
                            key==="images"?
                            <form onSubmit={handleSubmit(onSubmit)}>
                                <div  style={{textAlign:"center"}}>
                                <label htmlFor="contained-button-file">
                                <Input 
                                onChange={(event) => {
                                    setSelectedImage(event.target.files[0]);
                                }}
                                accept="image/*" id="contained-button-file" multiple type="file" />
                                <Button onClick={()=>setVariationId(item._id)} variant="contained" component="span" endIcon={<CameraAltIcon />}>
                                Upload image
                                </Button>
                                </label>
                                </div>
                                {!Array.isArray(selectedImage)&&<div style={{position:"fixed",bottom:"5%",right:"5%"}}>
                                <Tooltip title="Add Variation IMG">
                                <Fab type='submit' color="primary" variant="extended">
                                    <CheckIcon sx={{ mr: 1 }} />
                                    Add Variation IMG
                                    </Fab>
                                </Tooltip>
                                </div>}
                            </form>
                            :key!=="_id"&&<div  className="col-3 row m-auto singlevaritem">
                                <div className="col-6">
                                    <p className='key'>{key}</p>
                                </div>
                                <div className="col-6">
                                    <p>{value}</p>
                                </div>
                                
                            </div>
                        ))}
                        </div>
                    ))
                }
            </section>
            {!Array.isArray(selectedImage)?(
                  <div className="my-3 row justify-content-center">
                  <img alt="not fount" width={"250px"} src={URL.createObjectURL(selectedImage)} />
                  <IconButton onClick={()=>setSelectedImage([])} aria-label="delete" size="small" color='error'>
                  <DeleteIcon fontSize="inherit" />
                </IconButton>
                  </div>
                ):null}

            {
                !_.isEmpty(details.variations)&&details.variations.map((item,index)=>(
                    <div className='variation-imgs'>
                    <h5>Variation {index+1}</h5>
                    <div className="row m-auto align-items-center">
                    {item.images.length>0?item.images.map(img=>(
                        <div key={index} onClick={()=>{
                            setVariationId(item._id)
                            setDeleteImage(img)
                            setOpen(true)}}>
                            <img src={img} alt={img} />
                        </div>
                    )):<p>No Image</p>
                }
                    </div>

                    </div>
                ))
            }
{/* axios.get(`${process.env.REACT_APP_DEVELOPMENT}/api/variation/image/${img}`)
                                .then(res=>{
                                    console.log(res)
                                    return res;
                                })
                                .catch(err=>{
                                    console.log(err)
                                }) */}
                <h4>Specification</h4>
            {
                !_.isEmpty(details.specification)&&details.specification.map((item,index)=>(
                    _.map(item,(value,key)=>(
                        <div key={index} className="row m-auto spec-cont">
                            <div className="col-6 key-cont">
                                <p className="key">{key}</p>
                            </div>
                            <div className="col-6 value-cont">
                                <p className="value">{value}</p>
                            </div>
                        </div>
                    ))
                ))
            }

            

            

</section>   }


             </div>
    </div>

    )
}

const mapStateToProps = ({EventUser})=>{
    return {
        user:EventUser
    }
}

export default connect(mapStateToProps)(ProductDetail)
