import React from 'react'
import "./Approvals.scss"
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import Dashhead from '../Dashhead';
import axios from 'axios'
import {connect} from 'react-redux'
import moment from 'moment';
import { DataGrid } from '@mui/x-data-grid';
import {renderName2,formatMoney,orderSubTotal,orderDiscount,orderTotal} from '../utils/Functions'
import {Button} from '@mui/material'
import TwoBDialog from '../utils/TwoBDialog'
import _ from 'lodash'
function MerchantInfo(props) {
    const [display,setDisplay]=React.useState(false)
    const [open,setOpen] = React.useState(false)
    const [docName,setDocName]=React.useState("")
    console.log(props)
    let merchantInfo = props.location.state;
    const handleApprove = ()=>{
        axios.post(`${process.env.REACT_APP_DEVELOPMENT}/api/user/setKYCTrue`,{userId:merchantInfo._id},{headers:{token:props.user.user}})
        .then(res=>{
            console.log(res)
            props.history.push("/approvals")
        })
        .catch(err=>{
            console.log(err)
        })
    }   

    const handleSubmit = ()=>{
        axios.post(`${process.env.REACT_APP_DEVELOPMENT}/api/user/deleteDoc/`,{docName,userId:merchantInfo._id},{headers:{token:props.user.user}})
        .then(res=>{
            console.log(res)
            props.history.push("/approvals")
        })
        .catch(err=>{
            console.log(err)
        })
    }

    return (
        <div className="row">
            <TwoBDialog 
            open={open}
            setOpen={setOpen}
            title="Delete Document"
            description="Are you sure you want to delete this document? Once deleted then Merchant will have to reupload again"
            leftButton="Cancel"
            rightButton="Delete"
            handleSubmit={handleSubmit}
            />
            <div className="col-xs-12 col-sm-12 col-md-2 col-lg-2 col-xl-2">
            <Dashhead id={5} display={display} />
            </div>

            <div className="col-xs-12 col-sm-12 col-md-10 col-lg-10 col-xl-10 dashboard-container" onClick={()=>display&&setDisplay(false)}>
            <span className="iconbutton display-mobile">
            <IconButton  size="large" aria-label="Menu" onClick={()=>setDisplay(true)}>
            <MenuIcon fontSize="inherit" />
             </IconButton>
             </span>

            <h1>{merchantInfo.name}</h1>
            {!merchantInfo.kyc&&<Button className="my-3" variant='contained' onClick={()=>handleApprove()}>Approve Merchant</Button>}
            <p>Email: {merchantInfo.email}</p>
            <p>MerchantId: {merchantInfo.merchantId}</p>
            <p>Mobile No: {merchantInfo.mobileNo}</p>
            <p>PostCode: {merchantInfo.postCode}</p>
            <p>Rating :{merchantInfo.rating}</p>
            <p>KYC: {merchantInfo.kyc.toString()}</p>
            <p>GSTIN: {merchantInfo.merchantData.gstin}</p>
            <p>Shop Address: {merchantInfo.merchantData.shopAddress}</p>
            {
                merchantInfo.merchantData.documents.length>0?
                merchantInfo.merchantData.documents.map((item,index)=>
                <div>
                <h3>{item.name}</h3>
                <img onClick={()=>{
                    setDocName(item.name)
                    setOpen(true)
                    }} style={{height:"50%",width:"50%"}} className="document-img" src={`${process.env.REACT_APP_DEVELOPMENT}/api/user/image/${item.img}`} alt={item.name} />
                </div>
                )
                :
                <p style={{color:"red"}}>Merchant Hasn't uploaded pictures yet</p>
            }
            
            {!merchantInfo.suspended?<Button color="error" onClick={()=>{
                axios.post(`${process.env.REACT_APP_DEVELOPMENT}/api/user/suspend`,{suspend:true,userId:merchantInfo._id},{headers:{token:props.user.user}}).then(res=>{
                        console.log(res)
                        props.history.push("/approvals")
                    })
            }} variant="contained">Terminate Merchant</Button>:
            <Button  onClick={()=>{
                axios.post(`${process.env.REACT_APP_DEVELOPMENT}/api/user/suspend`,{suspend:false,userId:merchantInfo._id},{headers:{token:props.user.user}}).then(res=>{
                        console.log(res)
                        props.history.push("/approvals")
                    })
            }} variant="contained">Active Merchant</Button>
            }
           

             </div>
    </div>
    )
}

const mapStateToProps = ({EventUser})=>{
    return {
        user:EventUser
    }
}

export default connect(mapStateToProps)(MerchantInfo)
