import React from 'react'
import "./CreateProducts.scss"
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import Dashhead from '../Dashhead';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import { TextField,Button,Alert,Chip } from '@mui/material';
import {connect} from 'react-redux'
import Autocomplete from '@mui/material/Autocomplete';
import DeleteOutlinedIcon from '@mui/icons-material/DeleteOutlined';
import Snackbar from '../utils/Snackbar'
import axios from 'axios'
import AddCircleIcon from '@mui/icons-material/AddCircle';
import CheckIcon from '@mui/icons-material/Check';
import {Fab,Tooltip} from '@mui/material';
import {useForm} from 'react-hook-form'
import _ from 'lodash'
function EditProduct(props) {
    const {handleSubmit,register,formState:{errors},setValue}=useForm()
    const [display,setDisplay]=React.useState(false)
    const [error,setError]=React.useState("")
    const [categories,setCategories]=React.useState([])
    const [snackbarOpen,setSnackbarOpen]=React.useState(false)
    //id hooks
    const [productId,setProductId]=React.useState("")

    //data hooks
    const [attributeData,setAttributeData]=React.useState([])
    const [attributekeyvalueData,setAttributekeyvalueData]=React.useState({})
    const [emiData,setEmiData]=React.useState([])
    const [specificationData,setSpecificationData]=React.useState([])
    const [variationData,setVariationData]=React.useState([])
    const [snackbarMessage,setSnackbarMessage]=React.useState("")
    //data hooks

    //textfield hooks
    const [monthCount,setMonthCount]=React.useState("")
    const [selectedCategory,setSelectedCategory]=React.useState("")
    const [downPayment,setDownPayment] = React.useState("")
    const [attributeKey,setAttributeKey]=React.useState("")
    const [attributeValue,setAttributeValue]=React.useState("")
    const [ak,setAk]=React.useState("")
    const [specificationKey,setSpecificationKey]=React.useState("")
    const [specificationValue,setSpecificationValue]=React.useState("")
    const [variation,setVariation]=React.useState({price:0})
    //textfield hooks

    let details = props.location.state;
    console.log("details",details)
    const getCategories = ()=>{
        axios.get(`${process.env.REACT_APP_DEVELOPMENT}/api/category/all`,{headers:{token:props.user.user}})
        .then(res=>{
            console.log(res);
            setCategories(res.data.result)
        })
        .catch(err=>{
            console.log(err);
            setError("Something went wrong")
        })
    }

    const getProductVariations= ()=>{
        axios.get(`${process.env.REACT_APP_DEVELOPMENT}/api/variation/product`,{productId:details._id},{headers:{token:props.user.user}})
        .then(res=>{
            console.log(res);
            setVariationData(res.data.result)
        })
        .catch(err=>{
            console.log(err);
            setError("Something went wrong")
        })
    }

    React.useEffect(()=>{
        getCategories()
        //getProductVariations()
        setValue("name",details.name)
        setValue("description",details.description)
        setValue("brandname",details.brand.name)
        setSelectedCategory(details.category.name)
        setEmiData(details.EMI_Options)
        setAttributekeyvalueData(details.attributes[0])
        setSpecificationData(details.specification)
        setVariationData(details.variations)
        setProductId(details._id)
    },[])
    
    const handleAttributeAdd = ()=>{
        console.log(attributeKey,attributeValue);
        if(attributeKey.length>0 && attributeValue.length>0){
            setError("")
            if(!attributekeyvalueData[attributeKey]){
                attributekeyvalueData[attributeKey]=[attributeValue]
            }else{
                attributekeyvalueData[attributeKey].push(attributeValue)
            }
            setAttributekeyvalueData({...attributekeyvalueData})
        }else{
            setError("Enter valid Attribute Data")
        }
    }
    // {
    //     name:data.name,
    //     description:data.description,
    //     category:categoryId,
    //     offers,
    //     brand:{name:data.brandname},
    //     attributes:attributekeyvalueData,
    //     specification:specificationData,
    //     //variations:variationData,
    //     EMI_Options:emiData
    // }
    const onSubmit = (data)=>{
        let categoryId = categories.filter(item=>item.name===selectedCategory)
        categoryId = categoryId[0]
        //625529e80d4b9a0004f28d21 SMARTPHONE
        let offers =  ["Bank Offer5% Cashback on Flipkart Axis Bank Card", 
        "Special PriceGet extra 500 off (price inclusive of discount)",
        "Get Google Nest hub(2nd gen) at just 4999",
        "Get Google Nest mini at just 1999"]
        console.log(data,categoryId,attributekeyvalueData,emiData,specificationData,variationData);
        axios.post(`${process.env.REACT_APP_DEVELOPMENT}/api/product/edit`,
        {
            "productId": details._id,
            "data": [
                {"propName": "name", "value": data.name},
                {"propName": "description", "value": data.description},
                {"propName": "brand", "value": {name:data.brandname}},
                {"propName": "offers", "value": offers},
                {"propName": "attributes", "value": attributekeyvalueData},
                {"propName": "specification", "value": specificationData},
                {"propName": "EMI_Options", "value": emiData},
            ]
        
        }
        ,{headers:{token:props.user.user}})
        .then(res=>{
            console.log(res)
            if(res.data.msg==="Error"){
                setError(res.data.result)
            }else{
                
                //props.history.push("/productdetail",res.data.result)
                setSnackbarOpen(true)
                setSnackbarMessage("Product Edited Successfully")
            }
        })
        .catch(err=>{
            console.log(err)
        })
    }




    return (
        <div className="row">
            <Snackbar open={snackbarOpen} setOpen={setSnackbarOpen} message={snackbarMessage} />
            <div className="col-xs-12 col-sm-12 col-md-2 col-lg-2 col-xl-2">
            <Dashhead id={2} display={display} />
            </div>

            <div className="col-xs-12 col-sm-12 col-md-10 col-lg-10 col-xl-10 dashboard-container" onClick={()=>display&&setDisplay(false)}>
            <span className="iconbutton display-mobile">
            <IconButton  size="large" aria-label="Menu" onClick={()=>setDisplay(true)}>
            <MenuIcon fontSize="inherit" />
             </IconButton>
             </span>

            <section className="create-product-section">


                <h1 className="heading">Create Product</h1>
                <form onSubmit={handleSubmit(onSubmit)}>
                <h3 className="">Product Info</h3>
                <TextField className="mb-4" {...register('name',{required:true})} id="outlined-basics" fullWidth label="Product Name" variant='outlined' />
                <TextField className="mb-4" {...register('description',{required:true})} id="outlined-basics" fullWidth label="Product Description" variant='outlined' />
                <TextField className="mb-4" {...register('brandname',{required:true})} id="outlined-basics" fullWidth label="Product Brand Name" variant='outlined' />
                <FormControl className="textfield">
                <FormLabel id="demo-row-radio-buttons-group-label">Category</FormLabel>
                <RadioGroup
                            row
                            aria-labelledby="demo-row-radio-buttons-group-label"
                            name="row-radio-buttons-group"
                            value={selectedCategory}
                            onChange={(e)=>setSelectedCategory(e.target.value)}
                        >
                {
                    categories.length>0?(
                        categories.map((item,index)=>(

                            <FormControlLabel key={index} value={item.name} control={<Radio />} label={item.name} />
                        ))
                    ):null
                }
                </RadioGroup>
                </FormControl>
                <hr />

                {/* emi options container START*/}
                    <h3>EMI Options</h3>
                    <div className="row m-auto align-items-center">
                    <div className="col-3">
                    <TextField onChange={(e)=>setMonthCount(e.target.value)} fullWidth id="outlined-basics"  label="Month Count" variant='outlined' />
                    </div>
                    <div className="col-4">
                    <TextField onChange={(e)=>setDownPayment(e.target.value)} fullWidth id="outlined-basics" label="Downpayment" variant='outlined' />
                    </div>
                    <div className="col-2">
                    <IconButton sx={{fontSize:"2.1em"}} onClick={()=>setEmiData([...emiData,{monthCount,downPayment}])} color="primary">
                            <AddCircleIcon fontSize="inherit"/>
                        </IconButton>
                    </div>
                    </div>

                    {emiData.length>0&&<section className="row mx-auto emi-head-cont">
                    {
                        emiData.map((item,index)=>(
                            <div className="emi-cont" key={index}>
                                <p>Month Tenure: <b>{item.monthCount}</b></p>
                                <p>Downpayment: <b>{item.downPayment}</b></p>
                                <div style={{textAlign:"center"}}>
                                <Button onClick={()=>setEmiData(emiData.filter(i=>i.monthCount!==item.monthCount))}>Remove</Button>
                                </div>
                            </div>
                        ))
                    }
                    </section>}
                {/* emi options container END*/}

                <div className="mt-5" />
                <hr />


                {/* attributes addition section  START   */}
                <h3 className="">Attributes of Product</h3>
                <div className="row mx-auto mb-3 align-items-center">
                    <div className="col-4">
                    <TextField fullWidth onChange={(e)=>setAk(e.target.value)} className="textfield" id="filled-basics" label="Enter Attribute Key" variant='filled' />
                    </div>
                    <div className="col-2">
                        <IconButton sx={{fontSize:"2.1em"}} onClick={()=>setAttributeData([...attributeData,ak])} color="primary">
                            <AddCircleIcon fontSize="inherit"/>
                        </IconButton>
                    </div>
                </div>
                {
                    attributeData.length>0&&
                    attributeData.map((item,index)=><Chip className="m-3" key={index} label={item} onDelete={()=>setAttributeData(attributeData.filter(attr=>attr!==item))} />)
                }


                <div className="row my-5 mx-auto align-items-center">
                <div className="col-6">
                <Autocomplete
                    inputValue={attributeKey}
                    onInputChange={(event, newInputValue) => {
                    setAttributeKey(newInputValue);
                    }}
                    id="controllable-states-demo"
                    options={attributeData}
                    fullWidth
                    renderInput={(params) => <TextField fullWidth {...params} label="Select Attribute Key" />}
                />
                </div>
                <div className="col-6">
                <TextField onChange={(e)=>setAttributeValue(e.target.value)} fullWidth id="outlined-basics" label="Attribute Value" variant='outlined' />
                </div>
                </div>

                <div className="my-4" style={{textAlign:"center"}}>
                <Button onClick={()=>handleAttributeAdd()} variant="contained">Add Attribute</Button>
                </div>
                {!_.isEmpty(attributekeyvalueData)&&<section className="key-value-container">
                {  
                _.map(attributekeyvalueData,(value,key)=>(
                    <div>
                    <div className="row m-auto key-value align-items-center">
                        <div className='col-3'>
                            <p className="key">{key}</p>
                        </div>
                        <div className="col-7 row m-auto align-items-center">
                            {
                                value.map(item=><div>
                                <p className='value'>{item}{value.length>1&&","}</p>
                                </div>
                                )
                            }
                        </div>
                        <div className="col-2 delete">
                            <IconButton onClick={()=>{
                                setAttributekeyvalueData(_.omit(attributekeyvalueData,[key]))
                            }}>
                                <DeleteOutlinedIcon />
                            </IconButton>
                        </div>
                    </div>
                    </div>
                ))
                }
                </section>}
                {/* attributes addition section  WEND   */}

               


                {/* specificatrion of product START*/}
                <h3 className="">Specification of Product</h3>
                <div className="row m-auto">
                    <div className="col-6">
                    <TextField onChange={(e)=>setSpecificationKey(e.target.value)} id="outlined-basics" fullWidth label="Specification Key" variant='outlined' />
                    </div>
                    <div className="col-6">
                    <TextField onChange={(e)=>setSpecificationValue(e.target.value)} id="outlined-basics" fullWidth label="Specification Value" variant='outlined' />
                    </div>
                </div>
                <div className="my-5" style={{textAlign:"center"}}>
                    <Button onClick={()=>{
                        let obj = {}
                        obj[specificationKey]=specificationValue
                        setSpecificationData([...specificationData,obj])}} variant="contained">Add Specification</Button>
                </div>
                {
                    specificationData.length>0&&
                    <section className="specification-div">
                        <h4>Product Specifications</h4>
                        {
                            specificationData.map((item,index)=>(
                                _.map(item,(value,key)=>(
                                <div className="row m-auto align-items-center" key={index} >
                                    <div className="col-5 key-cont">
                                        <p>{key}</p>
                                    </div>
                                    <div className="col-5 val-cont">
                                        <p>{value}</p>
                                    </div>
                                    <div className="col-2 delete" >
                                    <IconButton onClick={()=>{
                                       setSpecificationData(specificationData.filter(i=>i[key]!==item[key]))
                                    }}>
                                        <DeleteOutlinedIcon />
                                    </IconButton>
                                    </div>
                                </div>
                                ))
                            ))
                        }
                    </section>
                }
                {/* specificatrion of product end*/}





                <h3>Add Variations of Product</h3>
                <p className="mb-5">Note: Before adding variations of product fill the above fields and click and on create product button then add variations</p>

                <div className="row m-auto variationrow">
                
                <div className="col-3">
                <TextField fullWidth onChange={(e)=>setVariation({...variation,price:e.target.value})} variant="filled" id="filled-basics" label="Price of variation" />
                </div>

                <div className="col-3">
                <TextField fullWidth onChange={(e)=>setVariation({...variation,mrp:e.target.value})} variant="filled" id="filled-basics" label="MRP of variation" />
                </div>
                <div className="col-6 row m-auto">
                {
                    !_.isEmpty(attributekeyvalueData)&&_.map(attributekeyvalueData,(value,key)=>(
                        <div className="mb-4 col-12">
                        <Autocomplete
                        disablePortal
                        id="combo-box-demo"
                        options={value}
                        fullWidth
                        onChange={(e,newValue)=>{
                            let obj = {}
                            obj[key]=newValue;
                            //console.log("var",newValue,key);
                            setVariation({...variation,...obj})
                        }}
                        renderInput={(params) => <TextField {...params} label={key} />}
                      />
                      </div>
                    ))
                }
                </div>
                </div>
                {
                    console.log("variation",variation)
                }
                <div className="my-5" style={{textAlign:"center"}}>
                <Button onClick={()=>{
                      axios.post(`${process.env.REACT_APP_DEVELOPMENT}/api/variation/create`,{options:variation,productId},{headers:{token:props.user.user}})
                      .then(res=>{
                        console.log(res);
                        setSnackbarOpen(true)
                        setSnackbarMessage("Variation Added Successfully")
                        setVariationData([...variationData,res.data.result])
                      })
                      .catch(err=>{
                          console.log(err)
                      })
                }} variant="contained">Add Varation</Button>
                </div>
                {
                    console.log(variationData)
                }

                {variationData.length>0&&
                <section className="variation-cont">
                {
                    variationData.map((item,index)=>(
                        <div className="row m-auto align-items-center variation-row" key={index}>
                        <div className="col-1">
                            <p>{index+1}</p>
                        </div>

                        <div className="col-10 row m-auto">
                        {_.map(item.options,(value,key)=>(
                            <div className="row m-auto col-4">
                                <div className="col-6">
                                    <p className='key'>{key} :</p>
                                </div>
                                <div className="col-6">
                                    <p className='value'>{value}</p>
                                </div>
                            </div>                         
                        ))   }
                        </div>

                        <div className="col-1">
                            <IconButton onClick={()=>{
                                axios.post(`${process.env.REACT_APP_DEVELOPMENT}/api/variation/delete`,{variationId:item._id},{headers:{token:props.user.user}})
                                .then(res=>{
                                    console.log(res)
                                    setVariationData(variationData.filter((i,ix)=>ix!==index))
                                })
                                .catch(err=>{
                                    console.log(err)
                                })
                                }}>
                            <DeleteOutlinedIcon />
                            </IconButton>
                        </div>


                        </div>
                        
                    ))
                }
                </section>}
                <div style={{position:"fixed",bottom:"5%",right:"5%"}}>
              <Tooltip title="Create Product">
              <Fab type='submit' color="primary" variant="extended">
                <CheckIcon sx={{ mr: 1 }} />
                Edit Product
                </Fab>
              </Tooltip>
              </div>

                </form>
            </section>
            

            {error.length>0&&<Alert className="alert" severity="error">{error}</Alert>}

             </div>
    </div>
    )
}

const mapStateToProps = ({EventUser})=>{
    return {
        user:EventUser
    }
}

export default connect(mapStateToProps)(EditProduct)



