import React from 'react'
import "./Approvals.scss"
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import Dashhead from '../Dashhead';
import axios from 'axios'
import { DataGrid } from '@mui/x-data-grid';
import moment from 'moment';
import {connect} from 'react-redux'
import CheckIcon from '@mui/icons-material/Check';
import {Fab,Tooltip} from '@mui/material';
import {Button} from '@mui/material'
import _ from 'lodash'
function Approvals(props) {
    const [display,setDisplay]=React.useState(false)
    const [data,setData]=React.useState([])
    const [merchantData,setMerchantData]=React.useState([])
    const [selectedData,setSelectedData]=React.useState([])

    //api/user/setKYCTrue {userId}
    const renderName2 = (product,options)=>{
        let name = product.name
        _.map(options,(value,key)=>{
            if(key!=="_id" && key!=="mrp" && key!=="price" && key!=="imgs"){
                name = name + " " + `(${value})`
            }
        })
        return name
    }
    const getApprovals = ()=>{
        axios.get(`${process.env.REACT_APP_DEVELOPMENT}/api/approval/all`,{headers:{token:props.user.user}})
        .then(res=>{
            console.log(res)
            let arr = res.data.result.map((item,index)=>({...item,id:index+1}))
            setData(arr)
            axios.get(`${process.env.REACT_APP_DEVELOPMENT}/api/user/allMerchant`,{headers:{token:props.user.user}})
            .then(res=>{
                console.log(res)
                let arr = res.data.result.map((item,index)=>({...item,id:index+1}))
                setMerchantData(arr)
            })
            .catch(err=>{
                console.log(err)
            })
        })
        .catch(err=>{
            console.log(err);
        })
    }

React.useEffect(()=>{
getApprovals()
},[])
console.log(selectedData)


    const handleReject = ()=>{
        axios.post(`${process.env.REACT_APP_DEVELOPMENT}/api/approval/reject`,{approvalId:selectedData,reason:"Default"},{headers:{token:props.user.user}})
        .then(res=>{
            console.log(res)
            getApprovals()
        })
        .catch(err=>{
            console.log(err)
        })
    }

    const handleDelete = ()=>{
        axios.post(`${process.env.REACT_APP_DEVELOPMENT}/api/approval/delete`,{approvalId:selectedData,reason:"Default"},{headers:{token:props.user.user}})
        .then(res=>{
            console.log(res)
            getApprovals()
        })
        .catch(err=>{
            console.log(err)
        })
    }

    const handleApprove = ()=>{
        axios.post(`${process.env.REACT_APP_DEVELOPMENT}/api/approval/accept`,{approvalId:selectedData},{headers:{token:props.user.user}})
        .then(res=>{
            console.log(res)
            getApprovals()
        })
        .catch(err=>{
            console.log(err)
        })
    }

    return (
        <div className="row">
            <div className="col-xs-12 col-sm-12 col-md-2 col-lg-2 col-xl-2">
            <Dashhead id={5} display={display} />
            </div>

            <div className="col-xs-12 col-sm-12 col-md-10 col-lg-10 col-xl-10 dashboard-container" onClick={()=>display&&setDisplay(false)}>
            <span className="iconbutton display-mobile">
            <IconButton  size="large" aria-label="Menu" onClick={()=>setDisplay(true)}>
            <MenuIcon fontSize="inherit" />
             </IconButton>
             </span>

            <h1>Merchant Approvals</h1>
            <div style={{ height: '50vh', width: '100%' }}>
                <DataGrid
                    rows={merchantData}
                    columns={columns2}
                    autoPageSize
                    onRowClick={(item,ev)=>{
                        console.log(item)
                        props.history.push("/merchantinfo",item.row)
                    }}
                />
                </div>




            <h1>Merchant Product Approvals</h1>

             <div style={{ height: '50vh', width: '100%' }}>
                <DataGrid
                    rows={data}
                    columns={columns}
                    autoPageSize
                    checkboxSelection
                    onRowClick={(item,ev)=>{
                        let sd = selectedData.filter(e=>e===item.row._id)
                        if(sd.length>0){
                            setSelectedData(selectedData.filter(e=>e!==item.row._id))
                        }else{
                            setSelectedData([...selectedData,item.row._id])
                        }
                    }}
                />
                </div>

                <div className="row mb-5 mt-3 mx-auto justify-content-between">
                    <Button onClick={()=>handleDelete()} variant='outlined'>Delete Selected</Button>
                    <Button onClick={()=>handleReject()}>Reject Selected</Button>
                    <Button onClick={()=>handleApprove()} variant="contained">Approve Selected</Button>
                </div>

             </div>
    </div>
    )
}


const mapStateToProps = ({EventUser})=>{
    return {
        user:EventUser
    }
}

export default connect(mapStateToProps)(Approvals)

const columns = [
    { field: 'id', headerName: 'ID',width:20},
    //{ field: 'brand', headerName: 'Brand Name',valueGetter:(param)=>param.value.name,width:150},
    { field: 'variationId', headerName: 'Variation Id',valueGetter:(param)=>param.value,width:150},
    { field: 'merchantDetails', headerName: 'Merchant Name',valueGetter:(param)=>param.value.name,width:150},
    { field: 'merchantEmail', headerName: 'Merchant Email',valueGetter:(param)=>param.row.merchantDetails.email,width:150},
    { field: 'merchantPhone', headerName: 'Merchant MobileNo',valueGetter:(param)=>param.row.merchantDetails.mobileNo,width:150},
    { field: 'merchantPan', headerName: 'Merchant Pan',valueGetter:(param)=>param.row.merchantDetails.panVeried.toString(),width:150},
    { field: 'status', headerName: 'Approval Status',valueGetter:(param)=>param.value,width:150},
    { field: 'quantity', headerName: 'Product Quantity',valueGetter:(param)=>param.value,width:150},
    { field: 'merchantId', headerName: 'Merchant Id',valueGetter:(param)=>param.row.merchantDetails._id.toString(),width:150},
    {field:"createdAt",headerName:"Created At",valueGetter:(param)=>moment.parseZone(param.value).local().format("DD/MM/YY"),width:120}


  ];

  const columns2 = [
    { field: 'id', headerName: 'ID',width:20},
    //{ field: 'brand', headerName: 'Brand Name',valueGetter:(param)=>param.value.name,width:150},
    { field: 'name', headerName: 'Merchant Name',valueGetter:(param)=>param.row.name,width:150},
    { field: 'merchantEmail', headerName: 'Merchant Email',valueGetter:(param)=>param.row.email,width:150},
    { field: 'merchantPhone', headerName: 'Merchant MobileNo',valueGetter:(param)=>param.row.mobileNo,width:150},
    { field: 'merchantId', headerName: 'Merchant Id',valueGetter:(param)=>param.row._id.toString(),width:150},
    { field: 'merchantKyc', headerName: 'KYC',valueGetter:(param)=>param.row.kyc.toString(),width:150},
    {field:"createdAt",headerName:"Created At",valueGetter:(param)=>moment.parseZone(param.value).local().format("DD/MM/YY"),width:120}


  ];