import React from 'react'
import "./Auth.scss"
import { TextField,Button,Alert } from '@mui/material'
import axios from 'axios'
import {connect} from 'react-redux'
import {setUser} from '../redux/user/userActions'

function VerifyOtp(props) {
    const [error,setError]=React.useState("")
    const [otp,setOtp]=React.useState("")
    console.log("verify otp",props)
    const getUser = (otp)=>{
        axios.post(`${process.env.REACT_APP_DEVELOPMENT}/api/auth/verifyOTP`,{...props.location.state,otp})
        .then(res=>{
            console.log(res);
            if(res.data.msg==="Success" && res.data.result!=="Not Found"){
                props.setUser(res.data.result)
                props.history.push("/dashboard")
            }else{
                setError("User doesn't exists")
            }
            
        })
        .catch(err=>{
            console.log(err);
            setError("Something went wrong")
        })
    }

    const handleSubmit = ()=>{
        // if(otp===props.location.state.data.otp){
        //    getUser()
        // }else{
        //     setError("Invalid OTP")
        // }
        getUser(otp)
    }

    const handleOtpChange = (otpm)=>{
        setOtp(parseInt(otpm))
        // if(parseInt(otpm)===props.location.state.data.otp){
        //    getUser()
        // }
        if(parseInt(otpm).length===6){
            getUser(otpm)
        }
    }

  return (
    <div className="auth-container">
        <img src="/cleverpeblack.png" alt="cleverpe" className="logo" />
        <h3>Welcome Admin</h3>
        <h6>OTP has been sent to {props.location.state.mobileNo}</h6>
        <p>{props.location.state.otp}</p>
        <TextField onChange={(e)=>handleOtpChange(e.target.value)} className="textfield" fullWidth variant="outlined" id="outlined-basic" label="Enter OTP" />
        <div style={{textAlign:"right"}}>
            <Button>Resend OTP</Button>
        </div>
        {error.length>0&&<Alert className="alert" severity="error">{error}</Alert>}
        <Button onClick={()=>handleSubmit()} className="mt-4" fullWidth variant="contained">Verify OTP</Button>
    </div>
  )
}

const mapDispatchToProps = (dispatch)=>{
    return {
        setUser:(token)=>dispatch(setUser(token))
    }
}

export default connect(null,mapDispatchToProps)(VerifyOtp)