import React from 'react'
import "./Home.scss"
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import Dashhead from './Dashhead';
import { BarChart, Bar, Cell, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';
import { PieChart, Pie, Sector   } from 'recharts';
import axios from 'axios'
import _ from 'lodash'
import { CChart } from '@coreui/react-chartjs';
import {connect} from 'react-redux'
import moment from 'moment';
import { DataGrid } from '@mui/x-data-grid';
function Home(props) {
    const [display,setDisplay]=React.useState(false)
    const [dashboardData,setDashboardData]=React.useState({})
    const [allOrderData,setAllOrderData] = React.useState([])

    React.useEffect(()=>{
      axios.get(`${process.env.REACT_APP_DEVELOPMENT}/api/product/dashboard`,{headers:{token:props.user.user}})
      .then(res=>{
        console.log(res)
        if(res.data.msg==="Success"){
          //let arr = res.data.result.map((item,index)=>({...item,id:index+1}))
          setDashboardData(res.data.result)
          axios.get(`${process.env.REACT_APP_DEVELOPMENT}/api/order/orderPerDay`,{headers:{token:props.user.user}})
          .then(res=>{
            console.log(res)
            setAllOrderData(res.data.result.orders[0].sixMonthOrders)
          })
          .catch(err=>{
            console.log(err)
          })
        }

      })
    },[])

    return (
        <div className="row">
            <div className="col-xs-12 col-sm-12 col-md-2 col-lg-2 col-xl-2">
            <Dashhead id={1} display={display} />
            </div>

            <div className="col-xs-12 col-sm-12 col-md-10 col-lg-10 col-xl-10 dashboard-container" onClick={()=>display&&setDisplay(false)}>
            <span className="iconbutton display-mobile">
            <IconButton  size="large" aria-label="Menu" onClick={()=>setDisplay(true)}>
            <MenuIcon fontSize="inherit" />
             </IconButton>
             </span>

           {!_.isEmpty(dashboardData)&&<section className="home-container">

            <div className="row m-auto justify-content-between align-items-center box-head">
                <div className="shadow col-2 box-content">
                    <h2>{dashboardData.orders[0].statusCount[0]&&dashboardData.orders[0].statusCount[0].count}</h2>
                    <h3>{dashboardData.orders[0].statusCount[0]&&dashboardData.orders[0].statusCount[0]._id.status} Orders</h3>
                </div>
                <div className="shadow col-2 box-content">
                    <h2>{dashboardData.orders[0].statusCount[1]&&dashboardData.orders[0].statusCount[1].count}</h2>
                    <h3>{dashboardData.orders[0].statusCount[1]&&dashboardData.orders[0].statusCount[1]._id.status} Orders</h3>
                </div>
                <div className="shadow col-2 box-content">
                    <h2>{dashboardData.orders[0].statusCount[2]&&dashboardData.orders[0].statusCount[2].count}</h2>
                    <h3>{dashboardData.orders[0].statusCount[2]&&dashboardData.orders[0].statusCount[2]._id.status} Orders</h3>
                </div>
                <div className="shadow col-2 box-content">
                    <h2>{Math.ceil(dashboardData.orders[0].AverageOrdersLast30[0].avgOrders)}</h2>
                    <h3>Merchant Orders/D</h3>
                </div>
            </div>

          <div className="row m-auto align-items-center">

            <div className="col-4">
            {!_.isEmpty(dashboardData)&&<CChart
              type="bar"
              data={{
                labels: dashboardData.orders[0].sixMonthOrders.map(item=>`${item._id.month}/${item._id.year}`),
                datasets: [
                  {
                    label: 'Number of orders',
                    backgroundColor: '#3705d2',
                    data: dashboardData.orders[0].sixMonthOrders.map(item=>item.count),
                  },
                ],
              }}
              labels="months"
            />}
            </div>

            <div className="col-3">
            {
              !_.isEmpty(dashboardData)&&
              <CChart
                type="doughnut"
                data={{
                  labels: dashboardData.users.map(item=>item._id.role),
                  datasets: [
                    {
                      backgroundColor: ['#000', '#3705d2'],
                      data: dashboardData.users.map(item=>item.count),
                    },
                  ],
                }}
              />
            }
            </div>


            </div>


            <section className='row m-auto'>

            <div className="col-8 my-3" style={{ height: '80vh', width: '100%' }}>
                <DataGrid
                    rows={dashboardData.orders[0].lastTenOrders.map((item,index)=>({...item,id:index+1}))}
                    columns={columns}
                    autoPageSize
                    onRowClick={(item,ev)=>props.history.push('/orderdetails',item.row)}
                />
                </div>


<div className='col-4'>
                <table className="ui celled table">
  <thead>
    <tr><th>Date</th>
    <th>Total Orders</th>
  </tr></thead>
  <tbody>
    {
      allOrderData.length>0&&allOrderData.map(item=>(
      <tr>
      <td data-label="Name">{item._id.day}/{item._id.month}/{item._id.year}</td>
      <td data-label="Job">{item.count}</td>
    </tr>
      ))
    }
    
  </tbody>
</table>
</div>
</section>
{/* 
            <div className="row m-auto">
                <div className="col-6">
                <BarChart
                    width={500}
                    height={300}
                    // data={[
                    //   {
                    //     name: dashboardData.orders[0].sixMonthOrders[],
                    //     uv: 4000,
                    //     pv: 2400,
                    //     amt: 2400,
                    //   },
                    //   {
                    //     name: 'Page B',
                    //     uv: 3000,
                    //     pv: 1398,
                    //     amt: 2210,
                    //   },
                  
                    // ]}
                    data = {dashboardData.orders[0].sixMonthOrders.map(item=>({name:`Month ${item._id.month}`,pv:item.count}))}
                    margin={{
                        top: 5,
                        right: 30,
                        left: 20,
                        bottom: 5,
                    }}
                    >
                    <CartesianGrid strokeDasharray="3 3" />
                    <XAxis dataKey="name" />
                    <YAxis />
                    <Tooltip />
                    <Legend />
                    <Bar dataKey="pv" fill="#8884d8" />
                    </BarChart>
                </div>
                <div className="col-6">
                <PieChart width={400} height={400}>
          <Pie
            data={data2}
            cx="50%"
            cy="50%"
            labelLine={false}
            label={renderCustomizedLabel}
            outerRadius={80}
            fill="#8884d8"
            dataKey="value"
          >
            {data.map((entry, index) => (
              <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
            ))}
          </Pie>
        </PieChart>
                </div>
            </div> */}



            </section>}

             </div>
    </div>
    )
}


const mapStateToProps =({EventUser})=>{
  return {
    user:EventUser
  }
}

export default connect(mapStateToProps)(Home)

