import React from 'react'
import "./Orders.scss"
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import { Button,TextField } from '@mui/material';
import Dashhead from '../Dashhead';
import axios from 'axios'
import {connect} from 'react-redux'
import moment from 'moment';
import { DataGrid } from '@mui/x-data-grid';
import {renderName2,formatMoney,orderSubTotal,orderDiscount,orderTotal} from '../utils/Functions'

import _ from 'lodash'
function OrderDetails(props) {
    const [display,setDisplay]=React.useState(false)
    const [orderDetails,setOrderDetails]=React.useState({})
    const [utr,setUtr]=React.useState("")
    const [edit,setEdit]=React.useState(false)
    //let orderDetails = props.location.state    
    console.log(orderDetails)

    const getOrderDetails = ()=>{
      axios.get(`${process.env.REACT_APP_DEVELOPMENT}/api/order/single/${props.location.state._id}`,{headers:{token:props.user.user}})
      .then(res=>{
        console.log(res)
        setOrderDetails(res.data.result)
      })
      .catch(err=>{
        console.log(err)
      })
    }

    const handlePaymentSent = ()=>{
      axios.post(`${process.env.REACT_APP_DEVELOPMENT}/api/order/merchantPayment`,{orderId:orderDetails._id,utr},{headers:{token:props.user.user}})
      .then(res=>{
        getOrderDetails()
        console.log(res)
      })
      .catch(err=>{
        console.log(err)
      })
    }

    const editUTR = ()=>{
      ///api/order/edit
      axios.post(`${process.env.REACT_APP_DEVELOPMENT}/api/order/edit`,{
        "data": [
        {"propName": "utr", "value": utr}
        ],
        "orderId":orderDetails._id
        },{headers:{token:props.user.user}})
        .then(res=>{
          console.log(res)
          setEdit(false)
          getOrderDetails()
        })
        .catch(err=>{
          console.log(err)
        })
    }


    React.useEffect(()=>{
      getOrderDetails()
    },[])

    return (
        <div className="row">
            <div className="col-xs-12 col-sm-12 col-md-2 col-lg-2 col-xl-2">
            <Dashhead id={4} display={display} />
            </div>

            <div className="col-xs-12 col-sm-12 col-md-10 col-lg-10 col-xl-10 dashboard-container" onClick={()=>display&&setDisplay(false)}>
            <span className="iconbutton display-mobile">
            <IconButton  size="large" aria-label="Menu" onClick={()=>setDisplay(true)}>
            <MenuIcon fontSize="inherit" />
             </IconButton>
             </span>

{!_.isEmpty(orderDetails)&&<section>

            <h1>Order ID: {orderDetails.id}</h1>
            <h4>_id: {orderDetails._id}</h4>
             <h2>Shipping Details</h2>
             {
                 _.map(orderDetails.shipping,(value,key)=>{
                     return <p>{key} : {value}</p>
                 })
             }
             <h2>Item Details</h2>
<table className="ui celled table">
      <thead>
        <tr><th>Name</th>
        <th>Sub Total</th>
        <th>Discount</th>
        <th>Total</th>
      </tr></thead>
      <tbody>
      {
      !_.isEmpty(orderDetails)&&
      orderDetails.items.map((item,index)=>(
        <tr key={index}>
          <td data-label="Name">{renderName2(item.details.product[0],item.details.options)} x {item.quantity}</td>
          <td data-label="Sub Total">{formatMoney(item.details.options.mrp)}</td>
          <td className="discounttable" data-label="Discount">{formatMoney(parseInt(item.details.options.mrp)-parseInt(item.details.options.price))}</td>
          <td className="totaltable" data-label="Total">{formatMoney(item.details.options.price)}</td>
        </tr>
      ))
      }
    

      </tbody>
    </table>

    <section className="payment-summary">
        <p className="subtotal">SubTotal: <b>{!_.isEmpty(orderDetails)&&orderSubTotal(orderDetails.items)}</b></p>
        <p className="discount">Discount: <b>{!_.isEmpty(orderDetails)&&orderDiscount(orderDetails.items)}</b></p>
        <p className="total">Total: <b>{!_.isEmpty(orderDetails)&&orderTotal(orderDetails.items)}</b></p>
      </section>


      <h5>Payment Acknowledgment of merchant: {orderDetails.merchantPayment.toString()}</h5>
      <div>
      {
        edit&&<div className="my-5">
          <TextField sx={{width:400}} onChange={e=>setUtr(e.target.value)} variant="outlined" id="outlined-basics" label="UTR Number" />
          <div>
          <Button onClick={()=>editUTR()} className="my-3" variant="contained">Edit UTR</Button><Button onClick={()=>setEdit(false)} className="my-3">Cancel</Button>
          </div>
          </div>
      }
      {!orderDetails.utr?<TextField sx={{width:400}} onChange={e=>setUtr(e.target.value)} variant="outlined" id="outlined-basics" label="UTR Number" />
      :
      <div>
      <p>UTR : {orderDetails.utr}</p>  
      <Button onClick={()=>setEdit(true)}>Click to Edit UTR</Button>
      </div>
    }
      </div>
      {!orderDetails.merchantPayment&&<Button onClick={()=>handlePaymentSent()} className="mt-4 mb-2" variant="contained">Payment sent to merchant</Button>}

      <h2>User Details</h2>
      <p>Name:{orderDetails.userDetails.name}</p>
      <p>Email:{orderDetails.userDetails.email}</p>
      <p>Mobile:{orderDetails.userDetails.mobileNo}</p>
      <p>Limit:{orderDetails.userDetails.creditLimit}</p>
      <h4>User Bank Details</h4>
      <p>Bank Account Number: {orderDetails.userDetails.merchantData.bankDetails&&orderDetails.userDetails.merchantData.bankDetails.bankaccontno}</p>
      <p>Bank IFSC Code: {orderDetails.userDetails.merchantData.bankDetails&&orderDetails.userDetails.merchantData.bankDetails.bankaccountholder}</p>
      <p>Bank Account Holder Name: {orderDetails.userDetails.merchantData.bankDetails&&orderDetails.userDetails.merchantData.bankDetails.bankifsc}</p>

      <h4>Addresses</h4>
      {
          orderDetails.userDetails.addresses.map(item=><p>{item}</p>)
      }
      {orderDetails.reciept?<img className="document-img" src={`${process.env.REACT_APP_DEVELOPMENT}/api/user/image/${orderDetails.reciept}`} alt="fornt" />:
      <p style={{color:"red"}}>Merchant Hasn't uploaded the reciept yet</p>
      }
</section>}
             </div>
    </div>
    )
}

const mapStateToProps = ({EventUser})=>{
    return {
        user:EventUser
    }
}

export default connect(mapStateToProps)(OrderDetails)
const columns = [
    { field: 'id', headerName: 'ID',width:20},
    //{ field: 'brand', headerName: 'Brand Name',valueGetter:(param)=>param.value.name,width:150},
    { field: 'name', headerName: 'Customer Name',valueGetter:(param)=>param.row.userDetails.name,width:150},
    { field: 'total', headerName: 'Total',valueGetter:(param)=>param.row.total,width:150},
    { field: 'status', headerName: 'Status',valueGetter:(param)=>param.row.status,width:150},
    { field: 'type', headerName: 'Type',valueGetter:(param)=>param.row.type,width:150},
    { field: 'totalitems', headerName: 'Total Items',valueGetter:(param)=>param.row.items.length,width:150},
    { field: 'mobileNo', headerName: 'Mobile Number',valueGetter:(param)=>param.row.userDetails.mobileNo,width:150},
    { field: 'address_1', headerName: 'Address 1',valueGetter:(param)=>param.row.type==="Onsite"?"":param.row.shipping.address_1,width:150},
    { field: 'address_2', headerName: 'Address 2',valueGetter:(param)=>param.row.type==="Onsite"?"":param.row.shipping.address_2,width:150},
    {field:"createdAt",headerName:"Created At",valueGetter:(param)=>moment.parseZone(param.value).local().format("DD/MM/YY"),width:120}


  ];